.place_container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 3em;
}

.place{
    display: block;
    background-color: rgba(0, 128, 0, 0.5);
    width: 40px;
    height: 40px;
    border-radius: 35px;
    cursor: pointer;
    transition: 0.3s;
}

.place_parkett{
    display: block;
    background-color: rgba(170, 39, 214, 0.5);
    width: 40px;
    height: 40px;
    border-radius: 35px;
    cursor: pointer;
    transition: 0.3s;
}

.place:hover{
    background-color: rgba(0, 128, 0, 1);
    transition: 0.3s;
}

.place_parkett:hover{
    background-color: rgba(170, 39, 214, 1);
}

.place_reserved{
    display: block;
    background-color: rgba(255, 0, 0, 0.5);
    width: 40px;
    height: 40px;
    border-radius: 35px;
    cursor: pointer;
    transition: 0.3s;
}



.place_reserved:hover{
    background-color: rgba(255, 0, 0, 1);
    transition: 0.3s;
}

