.sidebar{
    width: 20%;
    color: orangered;
}

.buttons{
    margin-top: 5vh;
    width: 70%;
    display: flex;
    justify-content: space-between;
}

.button{
    border: 4px solid purple;
    background-color: deeppink;
    border-radius: 15px;
    padding: 2% 3%;
    color: #ba0f0f;
    font-size: 1.1em;
    font-weight: 700;
    cursor: pointer;
}